.header-logo {
    width: 150px;
}

.navbar-nav {
    width: 100%;
    display: flex;
}

.nav-link {
    margin: 0 0.85rem;
}

.user-panel-icon {
    cursor: pointer;
    padding: 1em;
    position: relative;
}

.cart-products-num {
    position: absolute;
    top: 0 !important;
}

.search {
    position: absolute;
    right: -120%;
    top: 100%;
    z-index: 10;
    opacity: 0; 
    visibility: hidden;
}

.open {
    visibility: visible;
    transition: opacity 0.25s ease-in;
    opacity: 1
}

@media (max-width: 992px) {
    .navbar {
        width: 100% !important;
        flex-wrap: wrap !important;
    }
    .user-panel {
        flex-grow: 1 !important;
    }
    .user-panel-items {
        justify-content: space-between;
        width: 50%;
        margin: auto;
    }
}

.cart-products-num {
    position: absolute;
    top: -0.7em;
}

